.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.background-main {
    position: relative;
    width: 1400px;
    height: 800px;
    left: 1px;
    top: 10px;
    background-color: white;
    z-index: 0;
    padding: 2px;
    margin:0px auto;
    overflow: auto;
    margin-top: 120px; 

}

.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

.flex-item {
    width: 80px;
    height: 80px;
}

.shoppingCart{
    position: fixed;
    width: 60px;
    height: 50px;
    right: 20px;
    bottom: 20px;
    background-color: rgb(255, 125, 58);
    z-index: 10;
    padding: 15px;
    border-radius: 10px;
}

.side-bar{
    position: relative;
    width: 140px;
    height: 700px;
    border: 1px;
    margin: 10px 0;
    padding: 10px;
}

.main-area{
    position: relative;
    width: 1150px;
    height: 700px;
    border: 1px;
    margin: 10px 0;
    padding: 10px;
    align-items: center;
}

.divider {
    width: 300px;
    height: 1px;
    left: 1000px;
    background-color: #282c3486;
}

.logo{
    width: 120px;
}

.cart_icon{
    width: 50px;
    justify-items: center;
    align-content: center;
}

.navBar-setting {
    top: 0;
    width: 100%;
    position: fixed;
    background-color:white;
    border-bottom: 1px ridge rgba(128, 128, 128, 0.6);
    z-index: 3;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropbtn {
    background-color:white;
    color: gray;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .dropbtn {
    background-color: lightgray;
}

.navBar-menu-text {
    text-shadow: none;
    font-size: small;
    color: gray;
    font-weight: 150;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    align-items: center;
}

.searchBar {
    align-items: flex-end;
}

.sideMenu-setting {
    text-shadow: none;
    font-size: small;
    font-weight: 150;
}

.sideMenu-font {
    font-size: small; 
    
}

.mainArea-card-container {
    width: 200px;
    display: grid;
    margin: 1px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  .mainArea-card {
    width: 200px;
    padding: 12px;
    margin: 5px;
    border: 1px ridge lightgray;
    text-align: center;
    border-radius: 10px;
    object-fit: contain;
  }
  
  .mainArea-img {
    width: 90%;
    text-align: center;
  }

  .mainArea-title {
    margin-top: 8px;
  }
  
  .mainArea-amount {
    margin-top: 8px;
    font-size: 12px;
    color: green;
  }
  
  .add-to-cart-button {
    margin-top: 16px;
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .add-to-cart-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }

  .popup, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}

.popup-content {
    position: absolute;
    overflow: scroll;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
    
}

.popup-img {
    width: 500px;
    height: 300px;
    align-items: center;
  }

.close-popup {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}

.popup-cart{
    width: 1200px;
    height: 400px;
    top: 0;
    left: 0;
    position: fixed;
}

.cart-container {
    position: fixed;
    width: 1400px;
    height: 200px;
    margin: 2px;
}

.cart-content{
    position: fixed;
    width: 1320px;
    height: 570px;
    top: 20%;
    left: 4%;
    line-height: 1.4;
    background: #f1f1f1;
    border-radius: 3px;
    overflow-y:auto;
}

.cart-card-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    padding: 17px 30px;
    align-items: center;
    justify-items: center;
}

.cart-row {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
}

.cart-header {
    position: fixed;
    display: grid;
    width: inherit;
    height: auto;
    background-color: lightgray;
    grid-template-columns: 1fr 1fr;
    z-index: 10;
    justify-content: center;
    align-items: center;
}

.cart-footer {
    position: fixed;
    display: grid;
    width: inherit;
    height: auto;
    background-color: lightgray;
    grid-template-rows: 1fr 1fr;
    z-index: 10;
    bottom: 2%;
}

.cart-card {
    position: relative;
    width: 200px;
    height: 340px;
    padding: 12px;
    margin: 5px;
    border: 1px ridge lightgray;
    text-align: center;
    border-radius: 10px;
    object-fit: contain;
    top: 100px;
}

.cart-img {
    width: 90%;
    align-items: center;
}

.addToCartBtn {
    background-color: transparent;
    border: 1px solid lightgray;
    font: gray;
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
  
  .addToCartBtn:hover {
    background-color: black;
    color: white;
    cursor: pointer;
  }
